export function convertDate(milliSecond:number|string) {
    const days = ['', '', '', '', '', '', ''];
    const data = new Date(milliSecond);  //Date 
  
    const year = data.getFullYear();    //0000 
    const month = data.getMonth() + 1;  // 0  +1
    const date = data.getDate();        // 
    const day = days[data.getDay()];    // 
    const hours = ('0' + data.getHours()).slice(-2); 
    const minutes = ('0' + data.getMinutes()).slice(-2);
    return `${year}.${month}.${date}. ${hours} ${minutes} (${day})`;
  }